    line-height: 28px; }
  .breadcrumb-item:last-child {
    color: #f3f2ef; }
    .breadcrumb-item:last-child::after {
      display: none; }

.search {
  background-color: #2f3136;
  color: #f3f2ef;
  display: flex;
  align-items: center;
  padding: 14px 16px;
  border-radius: 7px; }
  .search input {
    margin-left: 12px;
    background-color: transparent;
    border: none;
    outline: none; }

.title {
  font-size: 16px;
  line-height: 24px;
  color: #f3f2ef;
