  justify-content: flex-end;
  align-items: center;
  margin: 10px;
  cursor: pointer;
  border: 1px solid transparent; }
  .document:hover {
    border-color: #6b6d72; }

.document.folder {
  min-height: 100px; }

.document-name {
  padding: 16px;
  color: #f3f2ef;
  font-size: 14px; }

.preview-image {
