.ellipsis-button {
  color: white; }

.dropdown {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  color: rgba(0, 0, 0, 0.65); }
  .dropdown.show {
    display: block; }

.dropdown-menu {
