.modal-mask {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 900;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45); }
  .modal-mask.show {
    display: block; }

.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 901;
  overflow: auto; }
  .modal.show {
    display: block; }

.modal-content {
