.cloud-monster-wrapper {
  position: absolute;
  bottom: 0;
  left: 0; }

.cloud-monster {
  position: absolute;
  left: -370px;
  bottom: -120px;
  transform: rotate(20deg);
  transition: all 0.5s;
  z-index: 701; }
  .cloud-monster.active {
    left: -264px;
    bottom: -135px;
    transform: rotate(0deg); }

.cloud-monster-modal.modal {
  display: block;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: unset;
  right: unset;
  left: 0;
  bottom: 0;
  width: auto;
  height: auto;
  z-index: 800;
  overflow: unset;
  transition: all 0.5s;
  z-index: 700; }
  .cloud-monster-modal.modal.show {
    left: 163px;
    bottom: 193px;
    visibility: visible;
    opacity: 1; }
  .cloud-monster-modal.modal .modal-content {
    width: 330px;
    margin: auto;
    position: relative; }
    .cloud-monster-modal.modal .modal-content::before {
      content: "";
      display: block;
      position: absolute;
      bottom: 0px;
      left: 0px;
      width: 20px;
      height: 10px;
      transform: rotate(-45deg) translateY(-50%);
      border-radius: 100% 0 0 100% / 50%;
      background-color: white;
      z-index: -1; }
  .cloud-monster-modal.modal .modal-header {
    margin-bottom: 0; }

.cloud-monster-modal-description {
